@import "./variables.scss";

body {
    margin: 0;
}

// Keyframe animations
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-10px);
    }
}

@keyframes glow {
    0%, 100% {
        opacity: 0.5;
        transform: scale(1);
    }
    50% {
        opacity: 0.8;
        transform: scale(1.05);
    }
}

@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(30px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.home {
    min-height: 100vh;
    font-family: $Lemonada;
    background: linear-gradient(135deg, $bg 0%, lighten($bg, 5%) 100%);
    position: relative;
    overflow-x: hidden;

    // Add subtle background pattern
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-image: 
            radial-gradient(circle at 25% 25%, rgba($primary, 0.1) 0%, transparent 50%),
            radial-gradient(circle at 75% 75%, rgba($secondary, 0.1) 0%, transparent 50%);
        pointer-events: none;
        z-index: 0;
    }

    &__hero {
        position: relative;
        z-index: 1;
        min-height: 90vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2rem 1rem;
        gap: 2rem;

        @media (min-width: $breakpoint-md) {
            flex-direction: row;
            gap: 4rem;
            padding: 2rem 4rem;
        }
    }

    &__picture {
        animation: fadeInUp 0.8s ease-out;

        &__wrapper {
            position: relative;
            display: inline-block;
        }

        &__glow {
            position: absolute;
            top: -20px;
            left: -20px;
            right: -20px;
            bottom: -20px;
            background: radial-gradient(circle, rgba($secondary, 0.3) 0%, transparent 70%);
            border-radius: 50%;
            animation: glow 3s ease-in-out infinite;
            z-index: -1;
        }

        img {
            border: 4px solid $secondary;
            border-radius: 50%;
            width: 200px;
            height: 200px;
            object-fit: cover;
            box-shadow: 
                0 10px 30px rgba($details, 0.3),
                0 0 0 10px rgba($secondary, 0.1);
            transition: all 0.3s ease;
            animation: float 6s ease-in-out infinite;

            &:hover {
                transform: scale(1.05);
                box-shadow: 
                    0 15px 40px rgba($details, 0.4),
                    0 0 0 15px rgba($secondary, 0.2);
            }

            @media (min-width: $breakpoint-md) {
                width: 300px;
                height: 300px;
            }
        }
    }

    &__content {
        text-align: center;
        max-width: 600px;
        
        @media (min-width: $breakpoint-md) {
            text-align: left;
        }
    }

    &__greeting {
        animation: fadeInUp 0.8s ease-out 0.2s both;

        &__text {
            font-family: $Nunito;
            font-size: 1.2rem;
            color: $details;
            font-weight: 500;
            opacity: 0.9;
        }
    }

    &__name {
        animation: fadeInUp 0.8s ease-out 0.4s both;
        margin: 0.5rem 0;

        &__first, &__last {
            display: block;
            font-family: $Nunito;
            font-weight: 700;
            color: $font;
            line-height: 1.1;
        }

        &__first {
            font-size: 2.5rem;
            
            @media (min-width: $breakpoint-md) {
                font-size: 3.5rem;
            }
        }

        &__last {
            font-size: 2.5rem;
            background: linear-gradient(45deg, $font, $secondary);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            
            @media (min-width: $breakpoint-md) {
                font-size: 3.5rem;
            }
        }
    }

    &__title {
        animation: fadeInUp 0.8s ease-out 0.6s both;
        margin: 1rem 0;

        &__main {
            display: block;
            font-family: $Nunito;
            font-size: 1.5rem;
            color: $details;
            font-weight: 600;
            
            @media (min-width: $breakpoint-md) {
                font-size: 1.8rem;
            }
        }

        &__sub {
            display: block;
            font-family: $Nunito;
            font-size: 1.2rem;
            color: $font;
            font-weight: 400;
            opacity: 0.8;
            
            @media (min-width: $breakpoint-md) {
                font-size: 1.4rem;
            }
        }
    }

    &__resume {
        animation: fadeInUp 0.8s ease-out 0.8s both;
        margin: 1.5rem 0 2rem;

        p {
            font-family: $Nunito;
            font-size: 1rem;
            line-height: 1.6;
            color: $font;
            opacity: 0.9;
            margin: 0;

            @media (min-width: $breakpoint-md) {
                font-size: 1.1rem;
            }

            strong {
                color: $details;
                font-weight: 600;
            }
        }
    }

    &__cta {
        animation: fadeInUp 0.8s ease-out 1s both;
        display: flex;
        gap: 1rem;
        flex-direction: column;
        align-items: center;

        @media (min-width: $breakpoint-md) {
            flex-direction: row;
            justify-content: flex-start;
        }

        &__primary, &__secondary {
            font-family: $Nunito;
            font-weight: 600;
            text-decoration: none;
            padding: 0.8rem 2rem;
            border-radius: 30px;
            transition: all 0.3s ease;
            display: inline-block;
            position: relative;
            overflow: hidden;
        }

        &__primary {
            background: linear-gradient(45deg, $secondary, $details);
            color: white;
            box-shadow: 0 4px 15px rgba($secondary, 0.3);

            &:hover {
                transform: translateY(-2px);
                box-shadow: 0 6px 20px rgba($secondary, 0.4);
            }

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: -100%;
                width: 100%;
                height: 100%;
                background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
                transition: left 0.5s;
            }

            &:hover::before {
                left: 100%;
            }
        }

        &__secondary {
            background: transparent;
            color: $details;
            border: 2px solid $details;

            &:hover {
                background: $details;
                color: white;
                transform: translateY(-2px);
                box-shadow: 0 4px 15px rgba($details, 0.3);
            }
        }
    }

    &__section {
        position: relative;
        z-index: 1;
        padding: 3rem 1rem;
        max-width: 1200px;
        margin: 0 auto;

        &__title {
            font-family: $Nunito;
            font-size: 2rem;
            color: $font;
            text-align: center;
            margin-bottom: 2rem;
            font-weight: 700;

            @media (min-width: $breakpoint-md) {
                font-size: 2.5rem;
            }
        }
    }

    &__quickActions {
        display: grid;
        grid-template-columns: 1fr;
        gap: 1.5rem;

        @media (min-width: 600px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width: $breakpoint-md) {
            grid-template-columns: repeat(4, 1fr);
        }

        &__card {
            background: white;
            border-radius: 15px;
            padding: 1.5rem;
            text-decoration: none;
            transition: all 0.3s ease;
            box-shadow: 0 4px 20px rgba($details, 0.1);
            border: 1px solid rgba($secondary, 0.1);
            position: relative;
            overflow: hidden;
            animation: fadeInUp 0.6s ease-out calc(var(--delay, 0s) + 1.2s) both;

            &:hover {
                transform: translateY(-5px);
                box-shadow: 0 8px 30px rgba($details, 0.2);
                border-color: $secondary;

                .home__quickActions__card__icon {
                    transform: scale(1.1);
                }

                .home__quickActions__card__arrow {
                    transform: translateX(5px);
                }
            }

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 3px;
                background: linear-gradient(90deg, $secondary, $details);
            }

            &__icon {
                font-size: 2rem;
                margin-bottom: 1rem;
                transition: transform 0.3s ease;
            }

            &__content {
                flex: 1;
            }

            &__title {
                font-family: $Nunito;
                font-size: 1.1rem;
                color: $details;
                margin: 0 0 0.5rem 0;
                font-weight: 600;
            }

            &__description {
                font-family: $Nunito;
                font-size: 0.9rem;
                color: $font;
                margin: 0;
                opacity: 0.8;
                line-height: 1.4;
            }

            &__arrow {
                position: absolute;
                top: 1rem;
                right: 1rem;
                font-size: 1.2rem;
                color: $secondary;
                transition: transform 0.3s ease;
            }
        }
    }

    &__highlights {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
        margin-top: 2rem;

        &__item {
            text-align: center;
            padding: 1.5rem;
            background: rgba(white, 0.7);
            border-radius: 15px;
            box-shadow: 0 4px 20px rgba($details, 0.1);
            transition: transform 0.3s ease;

            &:hover {
                transform: translateY(-3px);
            }
        }

        &__number {
            font-family: $Nunito;
            font-size: 2.5rem;
            font-weight: 700;
            color: $details;
            margin-bottom: 0.5rem;

            @media (min-width: $breakpoint-md) {
                font-size: 3rem;
            }
        }

        &__label {
            font-family: $Nunito;
            font-size: 0.9rem;
            color: $font;
            font-weight: 500;
            opacity: 0.8;

            @media (min-width: $breakpoint-md) {
                font-size: 1rem;
            }
        }
    }
}